<template>
  <div class="content">
    <div v-bind:class="{ zindex10: isActiveForm }" class="content-header">
      <div class="content-header-text">Nhân viên</div>
      <button
        @click="
          async () => {
            await getNewEmployeeCode();
            toggleFormEmployee();
            formMode = 'post';
          }
        "
        id="btnAdd"
        class="m-btn"
      >
        Thêm nhân viên
      </button>
      <EmployeeForm
        :toggleFormEmployee="toggleFormEmployee"
        v-if="isActiveForm"
        :employee="employeeSelected"
        :Loading="isLoading"
        :reload="getList"
        :mode="formMode"
      />
    </div>
    <div class="content-toolbar">
      <div class="m-input-wrapper">
        <input
          type="text"
          class="m-input placeholder-italic"
          v-model="searchVal"
          @input="filterEmployee"
          placeholder="Tìm kiếm theo mã, tên nhân viên"
        />
        <div class="m-icon m-input-icon"></div>
      </div>
      <div @click="getList" class="m-icon refresh-btn"></div>
    </div>
    <div class="m-table-container">
      <div class="m-table-wapper" style="min-height: 690px">
        <table class="m-table">
          <thead>
            <tr @click="getList">
              <th
                style="left: 0px"
                class="text-align-center pos-sticky sub-border-left"
              >
                <div
                  @click="
                    (e) => {
                      e.stopPropagation();
                    }
                  "
                  style="width: 22px"
                  class="m-row"
                >
                  <div class="checkbox">
                    <div class="checkbox-group">
                      <input type="checkbox" id="html" />
                      <label
                        @click="checkedAll = !checkedAll"
                        for="html"
                      ></label>
                    </div>
                  </div>
                </div>
              </th>
              <th style="left: 43px" class="text-align-left pos-sticky">
                <div style="width: 125px">Mã nhân viên</div>
              </th>
              <th style="left: 189px" class="text-align-left pos-sticky">
                <div style="width: 150px">Tên nhân viên</div>
              </th>
              <th class="text-align-left">
                <div style="width: 90px">Giới Tính</div>
              </th>
              <th class="text-align-center">
                <div style="width: 130px">Ngày sinh</div>
              </th>
              <th class="text-align-left">
                <div style="width: 150px">Số CMND</div>
              </th>
              <th class="text-align-left">
                <div style="width: 150px">Chức Danh</div>
              </th>
              <th class="text-align-left">
                <div style="width: 150px">Tên Đơn Vị</div>
              </th>
              <th class="text-align-left">
                <div style="width: 150px">Số Tài Khoản</div>
              </th>
              <th class="text-align-left">
                <div style="width: 150px">Tên Ngân Hàng</div>
              </th>
              <th class="text-align-left">
                <div style="width: 300px">Chi Nhánh TK Ngân Hàng</div>
              </th>
              <th
                style="right: 42px"
                class="text-align-center pos-sticky sub-border-right"
              >
                <div style="width: 100px; border-right: 0">Chức Năng</div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(employee, index) in employees"
              :key="index"
              @dblclick="
                getEmployeeSelected(employee);
                formMode = 'put';
              "
              ref="{{employee.EmployeeId}}"
              @click="rowClick"
            >
              <td
                style="left: 0px"
                class="text-align-center pos-sticky sub-border-left"
              >
                <div style="width: 22px" class="m-row">
                  <div class="checkbox">
                    <div class="checkbox-group">
                      <input
                        v-bind:id="employee.EmployeeId"
                        type="checkbox"
                        class="employee-checkbox"
                        v-bind:checked="checkedAll"
                      />
                      <label v-bind:for="employee.EmployeeId"></label>
                    </div>
                  </div>
                </div>
              </td>
              <td style="left: 43px" class="text-align-left pos-sticky">
                <div style="text-transform: uppercase">
                  {{ employee.EmployeeCode }}
                </div>
              </td>
              <td style="left: 189px" class="text-align-left pos-sticky">
                <div style="text-transform: capitalize">
                  {{ employee.EmployeeName }}
                </div>
              </td>
              <td class="text-align-left">
                <div>{{ employee.GenderName }}</div>
              </td>
              <td class="text-align-center">
                <div>{{ formatDayMonthYear(employee.DateOfBirth) }}</div>
              </td>
              <td class="text-align-left">
                <div>{{ employee.IdentityNumber }}</div>
              </td>
              <td class="text-align-left">
                <div>{{ employee.PositionName }}</div>
              </td>
              <td class="text-align-left">
                <div>{{ employee.DepartmentName }}</div>
              </td>
              <td class="text-align-left">
                <div>{{ employee.BankAccountNumber }}</div>
              </td>
              <td class="text-align-left">
                <div>{{ employee.BankName }}</div>
              </td>
              <td class="text-align-left">
                <div>{{ employee.BankBranchName }}</div>
              </td>
              <td
                style="right: 42px"
                class="text-align-center pos-sticky sub-border-right"
              >
                <div style="border-right: 0">
                  <span
                    @click="
                      getEmployeeSelected(employee);
                      formMode = 'put';
                    "
                    style="
                      color: #0075c0;
                      padding-left: 12px;
                      font-weight: Bold;
                      cursor: pointer;
                    "
                    >Sửa</span
                  >
                  <span
                    @click="handShowleDeleteBtn"
                    @dblclick="handShowleDeleteBtn"
                    class="m-modify-arrow"
                  >
                    <div
                      @click="
                        isShowMessageDelete = true;
                        employeeSelected = employee;
                      "
                      class="m-dropdown btnDelete"
                    >
                      Xóa
                    </div>
                  </span>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="m-pagination">
      <div class="left-inner">
        <p>
          Tổng số: <strong>{{ employeesNumber }}</strong> bản ghi
        </p>
      </div>
      <div class="right-inner">
        <ComboBoxRecord
          :selectNumberRecord="selectNumberRecord"
          style="margin: 0 16px"
        />
        <div
          class="flex-center"
          style="min-width: 140px; justify-content: space-between"
        >
          <div
            class="m-page-previous"
            @click="handlePageNumber('-1')"
            v-bind:class="{ blur: currentPageNumber === 1 }"
          >
            Trước
          </div>
          <div class="m-page-number">
            <div v-if="totalPageNumber < 5">
              <span
                v-for="(page, index) in totalPageNumber"
                :key="index"
                @click="
                  currentPageNumber = page;
                  handlePageNumber(page);
                "
                v-bind:class="{ active: currentPageNumber == page }"
                >{{ page }}</span
              >
            </div>
            <div v-if="totalPageNumber >= 5">
              <span
                v-for="(page, index) in 3"
                :key="index"
                @click="
                  currentPageNumber = page;
                  handlePageNumber(page);
                "
                v-bind:class="{ active: currentPageNumber == page }"
                >{{ page }}</span
              >
              <span>...</span>
              <span @click="currentPageNumber = totalPageNumber">{{
                totalPageNumber
              }}</span>
            </div>
          </div>
          <div
            class="m-page-next"
            @click="handlePageNumber('+1')"
            v-bind:class="{ blur: currentPageNumber === totalPageNumber }"
          >
            Sau
          </div>
        </div>
      </div>
    </div>
    <TheLoading v-show="isLoading == true" />
    <MessageDelete
      :employeeCode="employeeSelected.EmployeeCode"
      v-show="isShowMessageDelete == true"
      :deleteEmployee="deleteEmployee"
    />
  </div>
</template>
<script>
import axios from "axios";
import EmployeeForm from "../../base/EmployeeForm.vue";
import TheLoading from "../../base/TheLoading.vue";
import MessageDelete from "../../base/MessageDelete.vue";
import ComboBoxRecord from "../../base/ComboBoxRecord.vue";

export default {
  components: {
    EmployeeForm,
    MessageDelete,
    TheLoading,
    ComboBoxRecord,
  },

  created() {
    try {
      this.getList();
    } catch (error) {
      console.log(error);
    }
  },

  mounted() {
    //xử lý cuộn table
    window.addEventListener("wheel", (e) => this.handScrollTable(e));
    //xử lý tắt hết nút xóa
    window.addEventListener("click", this.handleHideDeleteBtn);
  },

  methods: {
    /**********************************************
     * Cuộn table khi có wheel event, chặn khi form nhân viên mở
     * Created by NDVan (22/2/2022)
     */
    handScrollTable(e) {
      if (e.deltaY > 0 && !this.isActiveForm) {
        document.querySelector(".m-table-container").classList.add("top-0");
      } else {
        document.querySelector(".m-table-container").classList.remove("top-0");
      }
    },

    /****************************************************************
     * Lấy danh sách NV theo số bản ghi và số trang
     * Created by NDVan (22/2/2022)
     */
    getList() {
      this.isLoading = true;
      this.employees = null;
      this.searchVal = null;
      axios
        .get(
          `http://amis.manhnv.net/api/v1/Employees/filter?pageSize=${this.pageSize}&pageNumber=${this.currentPageNumber}`
        )
        .then((res) => {
          this.employees = res.data.Data;
          this.totalPageNumber = res.data.TotalPage;
          this.employeesNumber = res.data.TotalRecord;
          this.isLoading = false;
        });
    },

    /**************************************************************************************
     * Định dạng lại thành Ngày/Tháng/Năm
     * @param (date) dob
     * Created by NDVan (22/2/2022)
     */
    formatDayMonthYear(dob) {
      let date = new Date(dob);
      // Lấy ngày, tháng , năm
      let day = date.getDate();
      let month = date.getMonth() + 1;
      let year = date.getFullYear();
      // định dạng lại ngày, tháng < 10
      day = day < 10 ? "0" + day : day;
      month = month < 10 ? "0" + month : month;
      // trả về ngày đúng format
      return `${day}/${month}/${year}`;
    },

    /**********************************************************************************
     * Định dạng lại ngày để bind vào input
     * Created by NDVan (22/2/2022)
     */
    formatYearMonthDay(oldDate) {
      let date = new Date(oldDate);
      // Lấy ngày, tháng , năm
      let day = date.getDate();
      let month = date.getMonth() + 1;
      let year = date.getFullYear();
      // định dạng lại ngày, tháng < 10
      day = day < 10 ? "0" + day : day;
      month = month < 10 ? "0" + month : month;
      // trả về ngày đúng format
      return `${year}-${month}-${day}`;
    },

    /************************************************
     * Tô đậm hàng khi row được click
     * Created by NDVan (22/2/2022)
     */
    rowClick(e) {
      document.querySelectorAll("tr").forEach((item) => {
        item.classList.remove("active");
      });
      e.currentTarget.classList.toggle("active");
    },

    /**********************************************
     * Mở/Đóng Form nhân viên
     * Created by NDVan (22/2/2022)
     */
    toggleFormEmployee() {
      this.isActiveForm = !this.isActiveForm;
    },

    /**********************************************
     *  Lấy mã nhân viên mới
     * Created by NDVan (22/2/2022)
     */
    async getNewEmployeeCode() {
      this.employeeSelected = {};
      await axios
        .get("http://amis.manhnv.net/api/v1/Employees/NewEmployeeCode")
        .then((res) => {
          this.employeeSelected.EmployeeCode = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    /*****************************************************************************
     * Lấy thông tin của nhân viên cần sửa, định dạng ngày và binding vào form
     * Created by NDVan (22/2/2022)
     */
    getEmployeeSelected(employee) {
      this.isLoading = true;
      // fix tạm thời DepartmentName vì Backend trả về là Null
      const d_name = employee.DepartmentName;
      axios
        .get("http://amis.manhnv.net/api/v1/Employees/" + employee.EmployeeId)
        .then((res) => {
          this.employeeSelected = res.data;

          // Định dạng lại ngày sinh: năm/tháng/ngày
          const dob = this.employeeSelected.DateOfBirth;
          this.employeeSelected.DateOfBirth = this.formatYearMonthDay(dob);
          // Định dạng lại ngày cấp: năm/tháng/ngày
          const iDate = this.employeeSelected.IdentityDate;
          this.employeeSelected.IdentityDate = this.formatYearMonthDay(iDate);

          this.employeeSelected.DepartmentName = d_name;
          this.isActiveForm = true;
          this.isLoading = false;
        });
    },

    /*********************************************
     * Xử lý z-index để hiển thị nút xóa
     * Created by NDVan (22/2/2022)
     */
    handShowleDeleteBtn(e) {
      //ẩn tất cả các nút xóa khác
      this.handleHideDeleteBtn();
      e.stopPropagation();
      // Thêm border cho icon down
      e.target.classList.add("active");
      e.target.children[0].classList.add("active");
      e.target.closest("td").classList.add("zIndex9");
    },

    /*********************************************
     * Xử lý để ẩn hết nút xóa
     * Created by NDVan (22/2/2022)
     */
    handleHideDeleteBtn() {
      document.querySelectorAll("table tr td:last-child").forEach((item) => {
        item.classList.remove("zIndex9");
      });
      [".m-modify-arrow", ".btnDelete"].forEach((parent) => {
        document.querySelectorAll(parent).forEach((child) => {
          child.classList.remove("active");
        });
      });
    },

    /*********************************************
     * Xóa nhân viên
     * Created by NDVan (22/2/2022)
     */
    deleteEmployee(bool) {
      if (bool) {
        this.isLoading = true;
        const id = this.employeeSelected.EmployeeId;
        this.isShowMessageDelete = false;
        axios
          .delete("http://amis.manhnv.net/api/v1/Employees/" + id)
          .then(() => {
            this.getList();
            this.isLoading = false;
          })
          .catch((err) => {
            console.log(err.response);
          });
      } else {
        this.isShowMessageDelete = false;
      }
    },

    /***********************************************************
     * Tìm kiếm thông tin nhân viên theo MÃ_HỌ TÊN_SỐ ĐIỆN THOẠI
     * Created by NDVan (26/2/2022)
     */
    filterEmployee() {
      const me = this;
      try {
        // sau khi ngừng gõ phím 500ms, gọi api để search
        clearTimeout(me.timer);
        me.timer = setTimeout(() => {
          me.employees = null;
          // Thực hiện gọi api để search với giá trị là input.value()
          axios
            .get(
              `http://amis.manhnv.net/api/v1/Employees/filter?employeeFilter=${me.searchVal}`
            )
            .then((res) => {
              // gán kết quả tìm kiếm backend trả về
              me.employees = res.data.Data;
              me.employeesNumber = me.employees?.length || 0;
            })
            .catch((err) => {
              console.log(err);
            });
        }, 500);
      } catch (err) {
        console.log(err);
      }
    },

    /***********************************************************
     *  Lựa chọn số bản ghi trên trang
     * Created by NDVan(26/2/2022)
     */
    selectNumberRecord(value) {
      this.pageSize = value;
      this.currentPageNumber = 1;
      this.getList();
    },

    /**
     * Xử lý logic phân trang
     * Created by NDVan(26/2/2022)
     */
    handlePageNumber(value) {
      let current = this.currentPageNumber;
      let total = this.totalPageNumber;
      if (value === "-1" && current > 1) {
        this.currentPageNumber = current - 1;
        this.getList();
      } else if (value === "+1" && current < total) {
        this.getList();
        this.currentPageNumber = current + 1;
      } else if (typeof value === "number") {
        this.currentPageNumber = value;
        this.getList();
      }
    },

    // xử lý logic page
    // nếu totalpage <= 4 , loop in 4

    // nếu totalpage >= 5
    // nếu currentPage <= 2 => loop 1 < i <= 3
    //nếu 2 < currentPage < maxpage - 2  =>  loop 2 < i < curentpage + 2
    //nếu currtentpage >= maxpage - 2 =>  loop currentpage < i < curentpage + 2
  },

  data() {
    return {
      employees: [],
      employeesNumber: null,
      //thông tin nhân viên được chọn để sửa
      employeeSelected: {},
      isShowMessageDelete: false,
      isDeleteEmployee: false,

      //bật, tắt form nhân viên (true => mở, false => đóng)
      isActiveForm: false,
      //check, uncheck tất cả checkbox (true => check, false => uncheck)
      checkedAll: false,

      isLoading: false,
      //chọn chế độ post hay put
      formMode: "",
      // giá trị cần search
      searchVal: null,
      // gán setTimeout
      timer: {},

      // số bản ghi / trang
      pageSize: 20,
      // trang hiện tại, mặc định = 1
      currentPageNumber: 1,
      // tổng số trang
      totalPageNumber: 1,
    };
  },
};
</script>
<style></style>
