<template lang="">
  <div class="m-message-container m-message-info">
    <div class="m-overlay"></div>
    <div class="m-message-box m-message-info">
      <div class="m-message-content">
        <div class="m-icon icon-message"></div>
        <div class="text-message">{{ message }}</div>
      </div>
      <div class="m-message-footer">
        <button @click="closeMessage" class="m-btn">Đóng</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "MessageInfo",
  props: ["msg_info", "closeMessage", "isMessageInfo"],
  beforeUpdate() {
    this.message = this.msg_info;
  },
  data() {
    return {
      message: null,
    };
  },
};
</script>
<style lang=""></style>
