<template lang="">
  <div class="m-combo-box">
    <div
      class="select-selected"
      @click="selectClick"
      v-bind:class="{ active: isAddBorder == true }"
      :tabindex="0"
    >
      {{ departmentNameVal || "Chọn phòng ban" }}
      <span class="select-selected-arrow"></span>
    </div>
    <div class="option-container" style="top: calc(100% + 3px)" v-show="isOpenOption">
      <div
        class="option"
        v-for="(department, index) in departmentData"
        :key="index"
        @click="
          departmentNameVal = department.DepartmentName;
          handleDepartment(department);
        "
      >
        {{ department.DepartmentName }}
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "ComboBoxDepartment",
  props: ["handleDepartment", "departmentName"],

  async mounted() {
    //tắt dropdown khi window click
    window.addEventListener("click", () => {
      this.isOpenOption = false;
      this.isAddBorder = false;
    });

    await this.getDepartmentData();

    //Thêm Background-Color cho Option khi click
    // const options = document.querySelectorAll(".option");
    // options.forEach((item) => {
    //   item.addEventListener("click", (e) => {
    //     // bỏ hết BG-color
    //     options.forEach((child) => {
    //       child.classList.remove("active");
    //     });
    //     // thêm BD-color cho option click
    //     e.target.classList.add("active");
    //   });
    // });

    // document.querySelector(".select-selected").addEventListener("blur", () => {
    //   document.querySelector(".m-combo-box").classList.remove("active");
    // });
  },

  methods: {
    /************************************************************
     * Lấy dữ liệu là ĐƠN VỊ từ trên serve
     * Created by NDVan (27/2/2022)
     */
    async getDepartmentData() {
      await axios
        .get("http://amis.manhnv.net/api/v1/Departments")
        .then((res) => {
          this.departmentData = res.data;
        });
    },

    /************************************************************
     * Xử lý logic của thẻ Select
     * Created by NDVan (27/2/2022)
     */
    async selectClick(e) {
      this.isAddBorder = true;
      e.stopPropagation();
      await this.getDepartmentData();
      this.isOpenOption = !this.isOpenOption;
    },
  },

  data() {
    return {
      departmentData: [],
      departmentNameVal: this.departmentName,
      isOpenOption: false,
      isAddBorder: false,
    };
  },
};
</script>
<style lang=""></style>
